img {
    height: auto;
    width: 100%;
    max-width: 150px;
    border: 1px;
    border-radius: 5px;
}

.grid-container {
    padding: 40px;
    border-radius: 10px;
}

.content-container {
    display: flex;
    justify-content: center;
    margin: 5px;
}

.button-container {
    display: flex;
    justify-content: center;
    margin: 5px;
    padding-left: 4px;
}

.image-container {
    display: flex;
    justify-content: center;
}

.column-item {
    margin: 5px;
    border: 1px solid lightgray;
    border-radius: 5px;
    min-width: 190px;
    transition: background-color .6s;
}

.column-item:hover {
    background-color: lightgray;
}